@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Epilogue:wght@500;700&family=Overpass:wght@400;700&family=Poppins:wght@300;400;500;600;700&display=swap');

:root {
    --primary-dark: #5651e5;
    --primary-light: #709dff;
}

@layer base {
    html {
        @apply scroll-smooth
    }

    body {
        @apply font-[Poppins]
    }

    li {
        @apply py-3 text-sm
    }

    button {
        @apply px-4 py-2 border bg-gradient-to-r from-[var(--primary-dark)] to-[var(--primary-light)] text-white rounded-md
    }

    h2 {
        @apply text-xl font-bold
    }
}